    margin-top: -36px;
  }
  .btn-search{
    margin-left: -55px;
  }
  .input-search{
    width: 325px
  }
  .text-com-head{
    text-align: center;
  }
}

.footer-copyright{
    background-color: #282c25;
    padding-top: 3px;
    padding-bottom: 3px;
}
.footer-copyright p {
    margin: 10px;
    color: #ccc;
}
.my-footer{
    background-color: #2c3e50;
    color: white;
}
.my-footer ul{
    list-style-type: none;
    padding-left: 0;
    line-height: 1.7;
}
.my-footer h5{
    font-size: 18px;
    color: white;
    font-weight: bold;
    margin-top: 30px;
}
.my-footer a{
    color: #d2d1d1;
    text-decoration: none;
}

.my-footer a:hover, .my-footer a:focus{
    text-decoration: none;
    color: white;
}

.social-networks{
    text-align: center;
    padding-top: 30px;
    padding-bottom: 38px;
}

.social-networks a {
    font-size: 32px;
    margin-right: 5px;
    margin-left: 5px;
    color: #f9f9f9;
    padding: 10px;
    transition: 0.2s;
}

.social-networks a:hover{
    text-decoration: none;
}

.social-networks .facebook:hover{
    color:#0077e2;
}

.social-networks .google:hover{
    color:#ef1a1a;
}

.social-networks .twitter:hover{
    color: #00aced;
}
.input-keyword{
    width: 100%;
    height: 100%;
}
.btn-keyword{
    width: 100%;
}
.img-food{
    height: 120px;
}
@media (max-width: 600px){
    .title-category{
        margin: 20px auto;
    }
    /* .list-group-item{
        margin: auto;
    } */
    .list-group{
        margin:auto;
        width:100%;
    }
    .col-keyword{
        margin-top: 20px;
    }
    .card-keyword{
        margin-top: 20px;
    }
    .input-keyword{
        width: 100%;
        height: 100%;
        font-weight: bold;
        font-size: 10.2px;
    }
    .btn-keyword{
        width: 100%;
        font-weight: bold;
        font-size: 10.5px;
        margin: auto;
    }
    .img-food{
        text-align: center;
    }
    .text-food{
        text-align: center;
    }
    .category-list{
        margin:auto;
    }
}
