.input-search{
  width: 100%;
}

.text-head{
  font-size: 40px;
  text-align: center;
}
@media (max-width: 600px){
  .city-name{
    font-size: 20px;
    text-align: justify;
  }
  .text-head{
    font-size: 34px;
    text-align: center;
  }
  .img-responsive{
    margin-top: 10px;
    height: 75%;
  }
  .row-welcome{
    margin-top: -36px;
  }
  .btn-search{
    margin-left: -55px;
  }
  .input-search{
    width: 325px
  }
  .text-com-head{
    text-align: center;
  }
}
